.footer {
    background-color: var(--container-color);
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.footer_container {
    padding: 2rem 0 6rem;
}

.footer_title,
.footer_link {
    color: var(--title-color);
}

.footer_title {
    text-align: center;
    margin-bottom: var(--mb-2);
}

.footer_link:hover {
    color: var(--title-color-dark);
}

.footer_list {
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
    margin-bottom: var(--mb-2);
}

.footer_social {
    display: flex;
    justify-content: center;
    column-gap: 1.125rem;
    margin-top: var(--mb-0-1);
}

.footer_social-link {
    background-color: var(--title-color);
    color: #fff;
    font-size: 1.25rem;
    padding: 0.4rem;
    border-radius: 0.5rem;
    display: inline-flex;
    width: 32.78px;
    height: 32.78px;
    align-items: center;
}

.iconfont {
    font-size: 20px;
}


.footer_social-link:hover {
    background-color: var(--title-color-dark);
}

/* ========== BREAKPOINTS ========== */
/*  For large devices */
@media screen and (max-width: 992px) {
    .footer_social-link {
        padding: 0.25rem;
        border-radius: 0.25rem;
        font-size: 1rem;
    }
  }